.container-fluid {
    display: flex;
    flex-flow: row nowrap;
    justify-content: space-evenly;
    align-items: flex-start;
    padding: 20px;


    article {
        padding: 25px;
        width: 60vw;

        .container_videoDetails {
            display: flex;
            margin: 0 0 50px 0;
            border: 1px solid #333;

            .videoDetails_cover {
                padding: 25px;
                border-radius: 5px;
            }
        }
    }
    aside {
        margin: 25px 30px 0 0;
        padding: 25px;
        border: 1px solid #333;

        table {
            text-align: center;
            caption {
                text-align: center;
            }
            thead {
                background-color: #333;
                color: #eee;
                th {
                    padding: 20px;
                }
            }
        }
    }
}



